.book {
    transform-style: preserve-3d;
    position: relative;
    backface-visibility: visible;
    transform-style: preserve-3d;
    perspective: 1200px;
}

.complete-book {
    width: 100%;
    height: 40vh;
}

.react-touch-events {
    width : 10vw;
    height: 5vh;
}

@media only screen and (max-width: 1024px) {

    .complete-book {
        height: 45vh;
    }

}